import React from 'react';
import PropTypes from 'prop-types';
import { Divider, NativeSelect, Select } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { bleDevice } from './ble'

const useStyles = makeStyles((theme) => ({
  box: {
    marginBottom: theme.spacing(0.2),
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  title: {
    fontSize: '150%',
    fontWeight: 'bold',
  },
  button: {
    margin: theme.spacing(0.5),
    fontSize : '120%',
  }
}));

const isCfThreshold       = 1;
const isVdThreshold       = 2;
const isGain              = 3;
const isNoiseSuppressor   = 4;
const isBeamForming       = 5;

const cfThresholdValue = [
   '70', ' 80',  '90', '100', '110',
  '120', '130', '140', '150',
];

const vdThresoldValue = [
   '5000',  '6000',  '7000',  '8000', '9000', 
  '10000', '11000', '12000', '13000',
]

const gainValue = [
  '1', '2', '3', '4', '5',
  '6', '7', '8', '9',
];

const noiseSuppressorValue = [
  '0', '1', '2', '3', 
  '4', '5', '6', '7',
];

const beamFormingValue = [
  '0', '1', '2', '3', 
  '4', '5', '6', '7',
];


const setParameters = (parameterId, value, wordId) => {
  const writeValue = value - 0; // string -> number

  bleDevice.writeValueWithoutResponse (bleDevice.bleInfo.device, 
                                       bleDevice.bleInfo.characteristicInfo.voiceRecogParams.characteristic, 
                                      [parameterId, (writeValue >> 8), (writeValue & 0xFF), wordId]);
}

function PageIsVoiceRecogSettings (props) {

  return (
    <div>

      <SelectConfideceThreshold wordInfo = {props.wordInfo} />

      <Divider/>

      <SelectVoiceDetectionThreshold wordInfo = {props.wordInfo}/>

      <Divider/>

      <SelectGain wordInfo = {props.wordInfo} />

      <Divider/>

      <SelectNoiseSuppressor wordInfo = {props.wordInfo} />

      <Divider/>

      <SelectBeamForming wordInfo = {props.wordInfo} />

    </div>
  );
}

function DrawButton (props) {
  const [currentValue, setCurrentValue] = React.useState('-1');
  const classes = useStyles();

  const handleButtonClicked = (value) => {
    
    if (!props.wordInfo[props.wordId]) {
      console.log('No wordInfo');
    }
    else if (props.wordId !== '0') {
      props.wordInfo[props.wordId].parameters[props.parameterId] = value - 0; // string -> number
    }
    else if (props.wordId === '0') {
      for (let index in props.wordInfo) {
        if (index !== '0') {
          props.wordInfo[index].parameters[props.parameterId] = value - 0; // string -> number
        }
        else {
          // nothing
        }        
      }
    }
    else {
      // nothing
    }
    
    setParameters(props.parameterId, value, props.wordId);
    setCurrentValue(value);
  }

  React.useEffect(() => {
    if (!props.wordInfo[props.wordId]) {
      console.log('No wordInfo');
    }
    else if (props.wordId !== '0') {
      setCurrentValue(props.wordInfo[props.wordId].parameters[props.parameterId]+ '');
    }
    else if (props.wordId === '0'){
      setCurrentValue(props.wordInfo[1].parameters[props.parameterId]+ '');
    }
    else{
      setCurrentValue('-1');
    }
    
  }, [props.wordInfo[props.wordId]]);

  return (
    <div>
        {props.value.map((value) => (
          ((value === currentValue) 
          ? 
            (
              <Button 
              className = {classes.button} 
              key = {value} 
              variant = 'outlined' 
              color = 'primary' 
              fontWeight = 'bold'
              onClick = {() => handleButtonClicked(value)}
              >
                {value}
              </Button>
            )
          :
            (
              <Button 
              className = {classes.button} 
              key = {value}
              onClick = {() => handleButtonClicked(value)}
              >
                {value}
              </Button>
            )
          )
        ))}
    </div>

  );
}

function SelectConfideceThreshold (props) {
  const classes = useStyles();
  const [wordId, setWordId] = React.useState(1);

  const handleWordIdChanged = (event) => {
    const value = event.target.value;

    if (value === '-1') {
      // nothing
    }
    else {
      setWordId(value);
    }    
  }

  return (
    <div>
      <Box className = {classes.box}>
        <Typography className = {classes.title}>
          Confidence Threshold
        </Typography>

        {
          ((!props.wordInfo[wordId]) 
          ?
            (
              <FormControl className={classes.formControl}>
              <InputLabel >Word ID</InputLabel>
              <Select
                native
                value={wordId}
                onChange={handleWordIdChanged}
              >
                <option aria-label='None' value={'-1'} />
              </Select>
              </FormControl>
            )
          :
            (
              <FormControl className={classes.formControl}>
              <InputLabel >Word ID</InputLabel>
              <Select
                native
                value={wordId}
                onChange={handleWordIdChanged}
              >
                {
                  props.wordInfo.map((info, index) => (
                    ((index === 0) ? 
                      (
                        <option value={'0'}>All</option>
                      )
                    :
                     (
                      <option value={info.wordId}>
                        No. {info.wordId}
                      </option>
                     )
                    )
                  ))
                }
              </Select>
              </FormControl>
            )
          )
        }

        <DrawButton 
        wordInfo = {props.wordInfo}
        value = {cfThresholdValue}
        parameterId = {isCfThreshold}
        wordId = {wordId}
        />
      </Box>
    </div>

  );
}

function SelectVoiceDetectionThreshold (props) {
  const classes = useStyles();
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Voice Detection Threshold
        </Typography>

        <DrawButton 
        wordInfo = {props.wordInfo}
        value = {vdThresoldValue}
        parameterId = {isVdThreshold}
        wordId = {1}
        />
      </Box>
    </div>
  );
}

function SelectGain (props) {
  const classes = useStyles();
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Gain
        </Typography>

        <DrawButton 
        wordInfo = {props.wordInfo}
        value = {gainValue}
        parameterId = {isGain}
        wordId = {1}
        />
      </Box>
    </div>
  );
}

function SelectNoiseSuppressor (props) {
  const classes = useStyles();
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Noise Suppressor
        </Typography>

        <DrawButton 
        wordInfo = {props.wordInfo}
        value = {noiseSuppressorValue}
        parameterId = {isNoiseSuppressor}
        wordId = {1}
        />
      </Box>
    </div>
  );
}

function SelectBeamForming (props) {
  const classes = useStyles();
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Beam Forming
        </Typography>

        <DrawButton 
        wordInfo = {props.wordInfo}
        value = {beamFormingValue}
        parameterId = {isBeamForming}
        wordId = {1}
        />
      </Box>
    </div>
  );
}


PageIsVoiceRecogSettings.propTypes = {
    window: PropTypes.func,
  };

export { PageIsVoiceRecogSettings };