import React from 'react';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';

// const isNotConnected  = 0;
// const isBeingDetected = 1;
const isUndetected    = 2;
const isFound         = 3;
const isNotFound      = 4;
const isUnconfidence  = 5;
const renesasBlue     = '#2a289d';
const renesasGray     = '#333333';

const voiceRecogStatusText = [
  'Click or tap "CONNECT" on the top right',
  'Detecting...',
  'Please say something!',
  'Success!',
  "No match",
  'Failed...',
];

function PageIsVoiceRecogResult (props) { 

  return (
    <div>

      <DrawVoiceRecogStatus 
      status = {props.voiceRecogResult.status} 
      wordId = {props.voiceRecogResult.wordId}
      score = {props.voiceRecogResult.score}
      wordInfo = {props.wordInfo}
      />

      <Divider/>

      <Box mt = {2}>
       <DrawList 
       wordInfo = {props.wordInfo}
       />
      </Box>

    </div>
  );
}

function DrawVoiceRecogStatus(props) {
  const [status, setStatus] = React.useState(props.status);
  const timeOutId = React.useRef(null);
  const statusText = React.useRef(voiceRecogStatusText[props.status]);
  const scoreText = React.useRef('');
  const wordText = React.useRef('');
  const statusTextStyle = React.useRef({
    fontSize: '150%', 
    fontWeight: 'bold',
    color: renesasGray,
  });
  const resultTextStyle = React.useRef({
    fontSize: '250%', 
    fontWeight: 'bold',
    color: renesasGray,
  });

  React.useEffect(() => {

    if (props.wordInfo.length) {
      switch (props.status) {
        case isFound :
          scoreText.current = "OK. Score is " + props.score + '.'
          wordText.current  = (props.wordId) + '. ' + props.wordInfo[props.wordId].text;
          resultTextStyle.current = {...resultTextStyle.current, color : renesasBlue};
        break;

        case isNotFound :
          scoreText.current = '';
          wordText.current = '';
        break;

        case isUnconfidence :
          scoreText.current = "NG. Score is " + props.score + '.'
          wordText.current  = (props.wordId) + '. ' + props.wordInfo[props.wordId].text;
          resultTextStyle.current = {...resultTextStyle.current, color : 'red'};
        break;

        default :
        break;
      }
    }
    else {
      scoreText.current = '';
      wordText.current = '';
    }

    statusText.current = voiceRecogStatusText[props.status];
    
    if (props.status === isUndetected) {
      switch (status) {
        case isFound : 
          timeOutId.current = setTimeout(() => {
            setStatus(props.status);
          }, 3000);
        break;

        case isNotFound :
          timeOutId.current = setTimeout(() => {
            setStatus(props.status);
          }, 3000);
        break;

        case isUnconfidence :
          timeOutId.current = setTimeout(() => {
            setStatus(props.status);
          }, 3000);
        break;

        default :
          setStatus(props.status);
        break;
      }
    }
    else {
      setStatus(props.status);
    }    

    return () => {
      if (timeOutId.current) clearTimeout(timeOutId.current);
      timeOutId.current = null;
    }   

  }, [props.status]);

  return (
    <Box mt={1} mb={2}>
      <Typography style = {statusTextStyle.current}>
        {statusText.current}
      </Typography>
      <Typography style = {resultTextStyle.current}>
        {scoreText.current} 
      </Typography>
      <Typography style = {resultTextStyle.current}>
        {wordText.current} 
      </Typography>
    </Box>
  );
}

function DrawList (props) {
  if (!props.wordInfo.length) {
    return (
      <div>
        {/* nothing */}
      </div>
    );
  }
  else {
    return(
      <Box>
        <Box mb = {1}>
          <Typography color = 'secondary' style = {{fontSize : '120%', fontWeight: 'bold'}}>
            List
          </Typography>
        </Box>
        {props.wordInfo.map((info, index) => (
          ((index === 0) ? <div></div> : 
          (<Box key = {info.wordId} >
            <Typography color = 'secondary' style = {{fontSize : '150%'}} >
              {info.wordId + '. ' + info.text}
            </Typography>
          </Box>)
        )))}
      </Box>

    );
  }
}

PageIsVoiceRecogResult.propTypes = {
    window: PropTypes.func,
  };

export { PageIsVoiceRecogResult };