
import React from 'react';
import PropTypes from 'prop-types';
import LinearProgress from '@material-ui/core/LinearProgress';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';

function PageIsIndoorEnvironment (props) {

    return (
      <div>

        <DrawSensorData 
        name = {"TEMP"}
        unit = {"Â°C"} 
        currentValue = {props.sensorData.temperature} 
        maxValue = {125} 
        minValue = {-40} 
        />

        <Divider />

        <DrawSensorData 
        name = {"HUMI"}         
        unit = {"%RH"} 
        currentValue = {props.sensorData.humidity} 
        maxValue = {100} 
        minValue = {0} 
        />

        <Divider />

        <DrawSensorData 
        name = {"CO2"}         
        unit = {"ppm"} 
        currentValue = {props.sensorData.co2} 
        maxValue = {10000} 
        minValue = {0} 
        />

        <Divider />

        <DrawSensorData 
        name = {"TVOC"}         
        unit = {"mg/m3"} 
        currentValue = {props.sensorData.tvoc} 
        maxValue = {50} 
        minValue = {0} 
        />

        <Divider />

        <DrawSensorData 
        name = {"IAQ"}         
        unit = {""} 
        currentValue = {props.sensorData.iaq} 
        maxValue = {50} 
        minValue = {0} 
        />

      </div>
    );
}

function DrawSensorData (props) {
  const [progress, setProgress] = React.useState(0);
  const timeOutId = React.useRef(null);  
  const margin = React.useRef('0%');
  const value = React.useRef(props.currentValue);

  React.useEffect(() => {
    timeOutId.current = setTimeout(() => { 
      const windowWidth = window.innerWidth
      const currentProgress = (props.currentValue - props.minValue) / (props.maxValue - props.minValue) * 100;
      value.current = props.currentValue;

      if (windowWidth < 300) {
        // small smartphone
        margin.current = currentProgress * 0.78 + '%';   
      }
      else if (windowWidth < 500) {
        // smartphone
        margin.current = currentProgress * 0.8 + '%';   
      }
      else if (windowWidth < 800) {
        // tablet
        margin.current = currentProgress * 0.9 + '%';   
      }
      else if (windowWidth < 1500) {
        // pc
        margin.current = currentProgress * 0.93 + '%';   
      }
      else {
        // monitor
        margin.current = currentProgress * 0.94 + '%';   
      }
      
      setProgress(currentProgress);
    }, 300);
    return () => {
      if (timeOutId.current) clearTimeout(timeOutId.current);
      timeOutId.current = null;
    }
  }, [props.currentValue]);

  return (
    <Box display = 'flex' flexDirection = 'row' width = '100%' mt={1} mb={2}>
      <Box minWidth = {80} mt = {1.5}>
        <Typography style = {{fontSize: '200%'}} color='secondary' >
          {props.name}
        </Typography>
      </Box>
      <Box width = '100%' ml = {2} mr= '35px'>
        <Box  display = 'flex' flexDirection = 'row' textAlign = 'center' >
          <Box ml = {margin.current}>
            <Typography style = {{fontSize: '150%', fontWeight: 'bold'}} color = 'primary'>
              {value.current}
            </Typography>
          </Box>
          <Box mt = {2.2}>
            <Typography style = {{fontSize: '80%', fontWeight: 'bold'}} color = 'secondary'>
            {props.unit}
            </Typography>
          </Box>
        </Box>
        <Box display = 'flex' flexDirection = 'row' alignItems = 'center'>
          <Box >
            <Typography  color = 'textSecondary' style = {{fontSize: '80%', fontWeight: 'bold'}}>
              {props.minValue}
            </Typography>
          </Box>
          <Box width = '100%' mr = {1} ml = {1}>
              <LinearProgress variant = 'determinate' value={progress} />
          </Box>
          <Box minWidth = '5%'>
            <Typography  color = 'textSecondary' style = {{fontSize: '80%', fontWeight: 'bold'}}>
              {props.maxValue}
            </Typography>
          </Box>          
        </Box>
      </Box>
    </Box>
  );
}

PageIsIndoorEnvironment.propTypes = {
    window: PropTypes.func,
  };

export { PageIsIndoorEnvironment };